import React, { useState, useEffect } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

import { Link } from 'react-router-dom';

import "./components/Register.css";
import Header from '../layouts/header-footer/Header';
import Footer from '../layouts/header-footer/Footer';

const RegisterForm: React.FC = () => {
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [birthDate, setBirthDate] = useState<Date | null>(null);
  const [age, setAge] = useState('');
  const [gender, setGender] = useState('');
  const [phone, setPhone] = useState('');
  const [email, setEmail] = useState('');
  const [address, setAddress] = useState('');
  const [errors, setErrors] = useState<{ [key: string]: string }>({});

  // Tnh tui
  useEffect(() => {
    if (!birthDate) {
      setAge('');
      setErrors(prev => ({ ...prev, birthDate: '' }));
      return;
    }

    const today = new Date();
    if (birthDate > today) {
      setAge('');
      setErrors(prev => ({ ...prev, birthDate: 'Ngy sinh khng c ln hn hin ti.' }));
      return;
    }

    let calculatedAge = today.getFullYear() - birthDate.getFullYear();
    const m = today.getMonth() - birthDate.getMonth();
    if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
      calculatedAge--;
    }

    setErrors(prev => ({ ...prev, birthDate: '' }));
    setAge(calculatedAge.toString());
  }, [birthDate]);

  const validateForm = () => {
    const newErrors: { [key: string]: string } = {};
    if (!firstName.trim()) newErrors.firstName = 'Vui lng nhp H.';
    if (!lastName.trim()) newErrors.lastName = 'Vui lng nhp Tn.';
    if (!birthDate) newErrors.birthDate = 'Vui lng chn ngy sinh.';
    if (!gender) newErrors.gender = 'Vui lng chn gii tnh.';
    if (phone.length !== 10) newErrors.phone = 'S in thoi phi  10 ch s.';
    if (!email.trim()) newErrors.email = 'Vui lng nhp email.';
    else if (!/\S+@\S+\.\S+/.test(email)) newErrors.email = 'Email khng hp l.';
    if (!address.trim()) newErrors.address = 'Vui lng nhp a ch.';
    setErrors(newErrors);

    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (validateForm()) {
      alert('ng k thnh cng!');
      // Gi d liu hoc x l tip theo
    }
  };

  return (
    <>
      <Header />
      <section className="form-container">
        <form className="register-form" onSubmit={handleSubmit}>
          <h2 id="register-title">ng k</h2>

          <div className="form-row-group">
            <label className="form-label">H v Tn</label>
            <div className="form-row">
              <div className="form-group" style={{ flex: 1 }}>
                <input
                  type="text"
                  placeholder="H"
                  className="input-text"
                  value={firstName}
                  onChange={(e) => setFirstName(e.target.value)}
                />
                {errors.firstName && <div className="error-text">{errors.firstName}</div>}
              </div>
              <div className="form-group" style={{ flex: 1 }}>
                <input
                  type="text"
                  placeholder="Tn"
                  className="input-text"
                  value={lastName}
                  onChange={(e) => setLastName(e.target.value)}
                />
                {errors.lastName && <div className="error-text">{errors.lastName}</div>}
              </div>
            </div>
          </div>


          <div className="form-row">
            <div className="form-group">
              <label className="form-label">Ngy sinh</label>
              <DatePicker
                selected={birthDate}
                onChange={(date: Date | null) => setBirthDate(date)}
                dateFormat="dd/MM/yyyy"
                className="input-text"
                placeholderText="dd/mm/yyyy"
                maxDate={new Date()}
                showMonthDropdown
                showYearDropdown
                dropdownMode="select"
                onKeyDown={(e) => {
                  const allowedKeys = ['Backspace', 'Delete', 'Tab', 'ArrowLeft', 'ArrowRight', '/'];
                  const isNumber = e.key >= '0' && e.key <= '9';
                  if (!isNumber && !allowedKeys.includes(e.key)) {
                    e.preventDefault();
                  }
                }}
              />
              {errors.birthDate && <div className="error-text">{errors.birthDate}</div>}
            </div>

            <div className="form-group">
              <label className="form-label">Tui</label>
              <input type="number" className="input-text" value={age} readOnly />
            </div>
          </div>

          <div className="form-group">
            <label className="form-label">Gii tnh</label>
            <select className="input-text" value={gender} onChange={(e) => setGender(e.target.value)}>
              <option value="">Nam/N</option>
              <option value="Nam">Nam</option>
              <option value="N">N</option>
              <option value="Khc">Khc</option>
            </select>
            {errors.gender && <div className="error-text">{errors.gender}</div>}
          </div>

          <div className="form-group">
            <label className="form-label">S in thoi</label>
            <input
              type="text"
              className="input-text"
              value={phone}
              onChange={(e) => {
                const input = e.target.value.replace(/\D/g, '');
                if (input.length <= 10) setPhone(input);
              }}
              onKeyDown={(e) => {
                const allowedKeys = ['Backspace', 'Delete', 'ArrowLeft', 'ArrowRight', 'Tab'];
                const isNumber = e.key >= '0' && e.key <= '9';
                if (!isNumber && !allowedKeys.includes(e.key)) {
                  e.preventDefault();
                }
              }}
              placeholder="Nhp s in thoi (10 s)"
            />
            {errors.phone && <div className="error-text">{errors.phone}</div>}
          </div>

          <div className="form-group">
            <label className="form-label">Email</label>
            <input
              type="email"
              className="input-text"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
            {errors.email && <div className="error-text">{errors.email}</div>}
          </div>

          <div className="form-row-group">
            <label className="form-label">a ch</label>
            <div className="form-row address-age-gender-row">
              <div className="form-group address-group">
                <textarea
                  className="input-text"
                  rows={4}
                  value={address}
                  onChange={(e) => setAddress(e.target.value)}
                ></textarea>
              </div>
            </div>
            {errors.address && <div className="error-text">{errors.address}</div>}
          </div>

          <div className="form-footer">
            <button type="submit" className="submit-btn">ng k</button>
            <Link to="/login" className="login-text">Bn  c ti khon ?</Link>
          </div>

        </form>
      </section>

      <Footer />
    </>
  );
};

export default RegisterForm;