import React, { useEffect } from 'react';

import './components/Home.css';
import bannerImg from './images/Banner/Banner1.png';
import { useNavigate } from 'react-router-dom';

import aaravImg from './images/User/Aarav.png';
import dinoyRajKImg from './images/User/DinoyRajK.png';
import rohanImg from './images/User/Rohan.png';

import calendarImg from './images/procedure/Calendar.png';
import healthCheckImg from './images/procedure/Health_check.png';
import donationImg from './images/procedure/Donation.png';
import afterDonationImg from './images/procedure/After_donation.png';

import newImg from './images/info/new_icon.png';
import askImg from './images/info/ask_icon.png';
import attentionImg from './images/info/attention_icon.png';
import bloodTypesImg from './images/info/bloodTypes_icon.png';
import joinImg from './images/info/join_icon.png';
import advisedImg from './images/info/advise_icon.png';
import bloodBankImg from './images/info/bloodBank_icon.png';

import Header from '../layouts/header-footer/Header';
import Footer from '../layouts/header-footer/Footer';

function Home() {
  const navigate = useNavigate();

  return (
    <div>
      <Header />
      <section className="banner-section">
        <img src={bannerImg} alt="Hin mu" className="banner-img" />
        <div className="banner-overlay" />
        <div className="banner-content">
          <h3>HIN MU</h3>
          <h1>V CNG NG</h1>
          <button className="book-btn" onClick={() => navigate("/register")}>KHM SNG LC</button>
        </div>
      </section>

      <section className="section">
        <div id="aboutus" className="mission">
          <h2>Trch nhim ca chng ti</h2>
          <p>Chng ti cam kt xy dng mt cu ni vng chc gia ngi hin mu v nhng ngi ang cn mu gp, gp phn cu sng hng nghn sinh mng mi ngy.</p>
          <p>S mnh ca chng ti l to ra mt nn tng minh bch, thun tin v an ton, thc y hin mu t nguyn, h tr cc trng hp khn cp v m bo ngun cung mu n nh cho cng ng.</p>
          <p>Cng vi cng ng v cc chuyn gia y t, chng ti n lc xy dng mt x hi giu lng nhn i, ni mi git mu s chia s mang n hy vng v s cha lnh.</p>
        </div>

        <h2>Blog</h2>
        <div className="blog-grid">
          {[
            {
              name: 'Aarav',
              image: aaravImg,
              avatar: aaravImg,
              title: 'Hin mu  iu nh, ngha ln',
              likes: 57,
              comments: 6,
              views: 138,
            },
            {
              name: 'Dinoy Raj K',
              image: dinoyRajKImg,
              avatar: dinoyRajKImg,
              title: 'Ln u i hin mu: Cm xc v tri nghim',
              likes: 72,
              comments: 4,
              views: 190,
            },
            {
              name: 'Rohan',
              image: rohanImg,
              avatar: rohanImg,
              title: 'Git mu cu ngi  Hnh trnh nhn i',
              likes: 102,
              comments: 12,
              views: 320,
            },
          ].map((blog, index) => (
            <div className="blog-card" key={index}>
              <img className="blog-img" src={blog.image} alt={blog.name} />
              <div className="avatar-container">
                <img className="avatar" src={blog.avatar} alt={blog.name} />
              </div>
              <div className="blog-body">
                <h4 className="blog-title">{blog.title}</h4>
                <div className="blog-footer">
                  <span> {blog.likes}</span>
                  <span> {blog.comments}</span>
                  <span> {blog.views}</span>
                </div>
              </div>
            </div>
          ))}
        </div>

        <h2>Quy trnh</h2>
        <div className="process-steps">
          {[
            { img: calendarImg, label: 'ng k t vn' },
            { img: healthCheckImg, label: 'Kim tra sc khe' },
            { img: donationImg, label: 'Hin mu' },
            { img: afterDonationImg, label: 'Theo di sc khe' },
          ].map((step, index) => (
            <div className="process-item" key={index}>
              <div className="icon-circle">
                <img src={step.img} alt={step.label} />
              </div>
              <p>{step.label}</p>
            </div>
          ))}
        </div>

        <br />

        <h2 id="info">Thng tin</h2>
        <div className="blood-section">
          <div className="left-column">
            <div className="info-box" onClick={() => navigate("/news")}>
              <img src={newImg} alt="Tin tc" className="icon" />
              Tin tc
            </div>
            <div className="info-box" onClick={() => navigate("/faqs")}>
              <img src={askImg} alt="Hi p" className="icon" />
              Hi p
            </div>
            <div className="info-box" onClick={() => navigate("/act")}>
              <img src={attentionImg} alt="Lu  khi hin mu" className="icon" />
              Cc hot ng hin mu nhn o
            </div>
            <div className="info-box" onClick={() => navigate("/bloodtype")}>
              <img src={bloodTypesImg} alt="Nhm mu" className="icon" />
              Nhm mu
            </div>
          </div>
          <div className="right-column">
            <div className="info-box" onClick={() => navigate("/standard")}>
              <img src={joinImg} alt="Tiu chun" className="icon" />
              Tiu chun tham gia hin mu
            </div>
            <div className="info-box" onClick={() => navigate("/advise")}>
              <img src={advisedImg} alt="advised" className="icon" />
              Nhng li khuyn trc v sau khi hin mu
            </div>
            <div className="info-box">
              <img src={bloodBankImg} alt="Ngn hng mu" className="icon" />
              Ngn hng mu
            </div>
          </div>
        </div>
      </section>
      
      <footer id="contact">
        {<Footer />}
      </footer>
    </div>
  );
}

export default Home;