import React, { useState } from "react"; 
import "./components/Login.css";
import loginImage from "./images/Banner/login_img.jpeg";

import { Link, useNavigate } from 'react-router-dom';

import Header from '../layouts/header-footer/Header';
import Footer from '../layouts/header-footer/Footer';

const Login: React.FC = () => {
  const navigate = useNavigate();

  // State  lu gi tr input
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    // Gi s check ng nhp n gin (bn thay bng api tht)
    if (username === 'admin' && password === '123') {
      // Lu trng thi ng nhp vo localStorage
      localStorage.setItem('isLoggedIn', 'true');
      localStorage.setItem('userName', username);

      // iu hng v trang ti khon hoc trang ch
      navigate('/admin'); // bn i ng dn ph hp
    } else if (username === 'user' && password === '123') {
      localStorage.setItem('isLoggedIn', 'true');
      localStorage.setItem('userName', username);
      navigate('/user'); // bn i ng dn ph hp
    } else {
      alert('Tn ng nhp hoc mt khu khng ng!');
    }
    
  };

  return (
    <div>
      <Header />

      {/* Main Login Form */}
      <main className="login-container">
        <div className="poster">
          <img src={loginImage} alt="Every Blood Donor is a Hero" />
        </div>
        <div className="login-form">
          <h2>ng nhp</h2>
          <form onSubmit={handleSubmit}>
            <input 
              type="text" 
              placeholder="Email hoc s in thoi" 
              required
              value={username}
              onChange={(e) => setUsername(e.target.value)}
            />
            <input 
              type="password" 
              placeholder="Mt khu" 
              required
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
            <button type="submit">ng nhp</button>
            <Link to="/forgot" className="forgot">Qun mt khu ?</Link>
          </form>
        </div>
      </main>

      <footer id="contact">
        <Footer />
      </footer>
    </div>
  );
};

export default Login;
