import React, { useState } from 'react';
import './components/Edit.css';

interface UserInfo {
  name: string;
  address: string;
  phone: string;
  email: string;
}

const Edit: React.FC = () => {
  // Gi s ly d liu user t API hoc context, mnh hardcode tm
  const [userInfo, setUserInfo] = useState<UserInfo>({
    name: 'Dinoy Raj K',
    address: '123 ng ABC, Qun 1, TP.HCM',
    phone: '0909123456',
    email: 'dinoy@example.com',
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setUserInfo({
      ...userInfo,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // Gi data ln API hoc x l lu info ngi dng
    alert('Thng tin  c lu thnh cng!');
    // V d: gi API  y
  };

  return (
    <div className="edit-container">
      <h2>Chnh sa thng tin ngi dng</h2>
      <form className="edit-form" onSubmit={handleSubmit}>
        <label htmlFor="name">Tn:</label>
        <input
          type="text"
          id="name"
          name="name"
          value={userInfo.name}
          onChange={handleChange}
          required
        />

        <label htmlFor="address">a ch:</label>
        <input
          type="text"
          id="address"
          name="address"
          value={userInfo.address}
          onChange={handleChange}
          required
        />

        <label htmlFor="phone">S in thoi:</label>
        <input
          type="tel"
          id="phone"
          name="phone"
          value={userInfo.phone}
          onChange={handleChange}
          required
          pattern="[0-9]{10,12}"
          title="S in thoi phi gm 10-12 ch s"
        />

        <label htmlFor="email">Email:</label>
        <input
          type="email"
          id="email"
          name="email"
          value={userInfo.email}
          onChange={handleChange}
          required
        />

        <button type="submit" className="save-btn">Lu thay i</button>
      </form>
    </div>
  );
};

export default Edit;
