import React, { useState } from 'react';
import './components/Admin.css';
import { Link } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import logoBlood from './images/Logo/logo_blood.png';

interface Account {
  id: number;
  name: string;
  email: string;
  role: 'Ngi dng' | 'Nhn vin y t' | 'Qun l kho mu';
}

const initialAccounts: Account[] = [
  { id: 1, name: 'Nguyn Vn A', email: 'a@example.com', role: 'Ngi dng' },
  { id: 2, name: 'Trn Th B', email: 'b@example.com', role: 'Nhn vin y t' },
  { id: 3, name: 'L Vn C', email: 'c@example.com', role: 'Qun l kho mu' },
  { id: 4, name: 'Phm Vn D', email: 'd@example.com', role: 'Ngi dng' },
  { id: 5, name: 'Hong Th E', email: 'e@example.com', role: 'Nhn vin y t' },
];

const adminName = 'Admin'; // bn c th ly t props, state, hoc context nu cn

const handleLogout = () => {
  // X l ng xut ti y
  alert('ng xut thnh cng!');
};

const Admin: React.FC = () => {
  const [accounts, setAccounts] = useState<Account[]>(initialAccounts);
  const [search, setSearch] = useState('');
  const [filterRole, setFilterRole] = useState<string>('Tt c');

  const handleDelete = (id: number) => {
    if (window.confirm('Bn c chc mun xa ti khon ny?')) {
      setAccounts(accounts.filter(account => account.id !== id));
    }
  };

  const filteredAccounts = accounts.filter(account => {
    const matchesRole = filterRole === 'Tt c' || account.role === filterRole;
    const matchesSearch = account.name.toLowerCase().includes(search.toLowerCase());
    return matchesRole && matchesSearch;
  });

  const roleCounts = {
    'Ngi dng': accounts.filter(a => a.role === 'Ngi dng').length,
    'Nhn vin y t': accounts.filter(a => a.role === 'Nhn vin y t').length,
    'Qun l kho mu': accounts.filter(a => a.role === 'Qun l kho mu').length,
  };

  return (
  <>
    <header className="admin-header">
      <div className="admin-logo">
        <Link to="/">
          <img src={logoBlood} alt="Logo" className="logo-img" />
        </Link>
      </div>
      <div className="admin-greeting">
        Xin cho, <span className="admin-name">{adminName}</span>
      </div>
      <button className="admin-logout-btn" onClick={handleLogout}>
        ng xut
      </button>
    </header>


    <div className="admin-container">
      <h1>Qun l ti khon</h1>

      <div className="role-summary">
        <div className="summary-box">
          <div className="summary-icon"></div>
          <div className="summary-role">Ngi dng</div>
          <div className="summary-count">{roleCounts['Ngi dng']}</div>
        </div>
        <div className="summary-box">
          <div className="summary-icon"></div>
          <div className="summary-role">Nhn vin y t</div>
          <div className="summary-count">{roleCounts['Nhn vin y t']}</div>
        </div>
        <div className="summary-box">
          <div className="summary-icon"></div>
          <div className="summary-role">Qun l kho mu</div>
          <div className="summary-count">{roleCounts['Qun l kho mu']}</div>
        </div>
      </div>

      <div className="admin-controls">
        <input
          type="text"
          placeholder="Tm theo tn..."
          value={search}
          onChange={e => setSearch(e.target.value)}
        />
        <select value={filterRole} onChange={e => setFilterRole(e.target.value)}>
          <option value="Tt c">Tt c</option>
          <option value="Ngi dng">Ngi dng</option>
          <option value="Nhn vin y t">Nhn vin y t</option>
          <option value="Qun l kho mu">Qun l kho mu</option>
        </select>
      </div>

      <table className="admin-table">
        <thead>
          <tr>
            <th>Tn</th>
            <th>Email</th>
            <th>Vai tr</th>
            <th>Hnh ng</th>
          </tr>
        </thead>
        <tbody>
          {filteredAccounts.length > 0 ? (
            filteredAccounts.map(account => (
              <tr key={account.id}>
                <td>{account.name}</td>
                <td>{account.email}</td>
                <td>{account.role}</td>
                <td>
                  <button className="edit-btn">Sa</button>
                  <button className="delete-btn" onClick={() => handleDelete(account.id)}>
                    Xa
                  </button>
                </td>
              </tr>
            ))
          ) : (
            <tr>
              <td colSpan={4}>Khng tm thy ti khon ph hp.</td>
            </tr>
          )}
        </tbody>
      </table>
    </div>
  </>
);
};

export default Admin;
