import React from 'react';
import './components/News.css';
import Header from '../layouts/header-footer/Header';
import Footer from '../layouts/header-footer/Footer';

const articles = [
    {
        title: "KHI NG THNG NHN O NM 2025: HNH TRNH...",
        image: "/images/news1.jpg",
        description: "Ngy 8-5, ti TPHCM, Trung ng Hi Ch thp  Vit Nam v UBND TPHCM phi hp t chc l pht ng Thng Nhn o...",
    },
    {
        title: "NGY TON DN HIN MU 7/4/2025",
        image: "/images/news2.jpg",
        description: "Ngy 7/4, chng ta cng nhau hng v mt ngy  ngha  Ngy Ton dn hin mu tnh nguyn.",
    },
    {
        title: "P DNG CNG NGH S TRONG HOT NG HIN...",
        image: "/images/news3.jpg",
        description: "Ngy 04/3, ti Trung tm Hin mu nhn o, Hi Ch thp  Thnh ph phi hp Hi Tin hc TP trin khai...",
    },
    {
        title: "HN 1.000 N V MU C HIN",
        image: "/images/news4.jpg",
        description: "Ngi dn tch cc tham gia hin mu trong chng trnh Hnh trnh , gp phn cu ngi kp thi.",
    },
    {
        title: "HI NGH TNG KT CNG TC HIN MU",
        image: "/images/news5.jpg",
        description: "Tng kt nm 2024 v trin khai k hoch vn ng hin mu nm 2025 trn ton quc.",
    },
    {
        title: "K NIM 25 NM THNH LP TRUNG TM",
        image: "/images/news6.jpg",
        description: "Trung tm Hin mu nhn o t chc l k nim 25 nm thnh lp v pht trin (19992024).",
    },
];

const News = () => {
    return (
        <div>
            <Header />

            <div className="news-container">
                {articles.map((item, index) => (
                    <div className="news-card" key={index}>
                        <img src={item.image} alt={item.title} />
                        <h3>{item.title}</h3>
                        <p>{item.description}</p>
                    </div>
                ))}
            </div>

            <Footer />
        </div>
    );
};

export default News;
